<template>
  <div class="build1">
    <back-home></back-home>
    <po-pup :buildIndex="buildIndex" class="mapLogo"></po-pup>
    <transition name="part1">
      <div class="part1" v-if="part1Show" @mousewheel.prevent="mouseWheel">
        <div class="banner" ref="banner1" :style="{ left: imgLeft + 'px' }">
          <img
            src="../../../assets/buildInfo/build1/业记素材-01-04.png"
            @load="imgLoad"
          />
        </div>
        <div class="word" ref="word1" :style="{ top: wordTop + 'px' }">
          <p class="pFirst">大名鼎鼎的《大公报》曾</p>
          <p>临时把报社设立在惠吉西二坊</p>
          <p>一号，是迄今中国发行时间最</p>
          <p>长的中文报纸，也是1949年以</p>
          <p>前影响力最大的报纸之一。</p>
        </div>
      </div>
    </transition>
    <transition name="part2">
      <div class="part2" v-if="part2Show">
        <div class="part2Index build1Shu" @mousewheel="mouseWheel2">
          <div
            class="Box"
            ref="index"
            :style="{ transform: `translateY(${picTop}px)` }"
          >
            <div class="Box1">
              <div
                class="imgBox1"
                :style="{ transform: `translateY(${SrollTop1}px)` }"
              >
                <img
                  src="@/assets/buildInfo/build1/业记素材-01-08.png"
                  alt=""
                  class="img1"
                />
              </div>
              <div class="content1">
                <p style="font-family: QingKai" class="content1P1">
                  大公报·英敛之
                </p>
                <p class="content1P2">大公报创始人，满洲正红旗人</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part3">
      <div
        class="part3 part part3Index"
        @mousewheel.prevent="mouseWheel3"
        v-if="part3Show"
      >
        <div class="part3Box" ref="part3Box">
          <div class="part3Box1">
            <div
              class="part3ImgBox1"
              :style="{ transform: `translateY(${part3PicTop}px)` }"
            >
              <img
                src="@/assets/buildInfo/build1/业记素材-01-11.png"
                alt=""
                class="part3Img1"
              />
            </div>
            <div class="part3Content1">
              <p
                style="font-family: QingKai"
                class="part3Content1P1"
                :style="{ transform: `translateX(${part3SrollTop1}px)` }"
              >
                建筑下的小报童雕塑，被刷上了金铜漆
              </p>
            </div>
            <div class="part3Content2">
              <p
                style="font-family: QingKai"
                class="part3Content1P2"
                :style="{ transform: `translateX(${part3SrollTop2}px)` }"
              >
                让人想起当时岭南新闻报业的光辉。
              </p>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BackHome from "../../../components/backHome.vue";
import PoPup from "../../../components/popup.vue";

export default {
  name: "build25",
  data() {
    return {
      wordTop: 0,
      imgLeft: 0,
      imgWidth: null,
      wordHeight: null,
      screenY: null,
      screenX: null,
      indexHeight: 0,
      part1Show: true,
      part2Show: false,
      part3Show: false,
      buildIndex: this.$route.query.index, //this.buildIndex是建筑的序号
      // buildIndex: 0,
      // 下面是part2内部的变量
      picTop: 0, //图片盒子的translateY
      BoxHeight: 0, //图片盒子元素的1/10高度
      srollY: 0,
      SrollTop1: 0,
      // 下面是part3内部的变量
      part3PicTop: 0, //图片盒子的translateY
      part3BoxHeight: 0, //图片盒子元素的1/9高度
      part3srollY: 0,
      part3SrollTop1: 0,
      part3SrollTop2: 0,
    };
  },
  components: {
    BackHome,
    PoPup,
  },
  mounted() {
    this.wordHeight = this.$refs.word1.offsetHeight;
    this.screenX = document.body.clientWidth;
    this.screenY = document.body.clientHeight;
  },
  methods: {
    mouseWheel(e) {
      //deltaY-125往上滚
      //下面页面往上顶
      if (e.deltaY > 0) {
        this.wordTop =
          this.wordTop - 15 < -(this.wordHeight - this.screenY)
            ? -(this.wordHeight - this.screenY)
            : this.wordTop - 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      } else {
        //往上滚
        this.wordTop = this.wordTop + 15 > 0 ? 0 : this.wordTop + 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      }
      if (this.wordTop <= -(this.wordHeight - this.screenY)) {
        this.part2Show = true;
        setTimeout(() => {
          this.BoxHeight = (1 / 10) * this.$refs.index.offsetHeight; //2/3元素的高度
        }, 0);
      } else {
        // 第二个页面消失
        this.part2Show = false;
      }
    },
    imgLoad() {
      this.imgWidth = this.$refs.banner1.offsetWidth;
    },
    mouseWheel2(e) {
      this.srollY += e.wheelDeltaY * 0.15;
      if (this.srollY > 0) {
        this.srollY = 0;
        this.picTop = this.srollY;
      } else if (this.srollY > ~this.BoxHeight) {
        this.picTop = this.srollY;
      } else {
        this.srollY = ~this.BoxHeight;
        this.picTop = ~this.BoxHeight;
      }
      this.SrollTop1 = this.srollY * 0.2;

      // 下面是页面跳转逻辑，上面是页面内的逻辑
      if (this.part2Show) {
        this.indexHeight =
          this.indexHeight == 0
            ? this.$refs.index.offsetHeight
            : this.indexHeight;
        if (this.$refs.index.scrollTop == 0 && e.deltaY < 0) {
          //第二part消失
          this.part2Show = false;
        }

        // if (
        //   this.$refs.index.scrollTop >= this.screenY + this.indexHeight - 50 &&
        //   e.deltaY > 0
        // ) {
        //   //第三part出现
        //   this.part3Show = true;
        // }
        if (
          this.picTop <= -(this.indexHeight - this.screenY) + 10 &&
          e.deltaY > 0
        ) {
          //第三part出现
          this.part3Show = true;
          setTimeout(() => {
            this.part3BoxHeight = (3 / 5) * this.$refs.part3Box.offsetHeight; //1/9元素的高度
          }, 0);
        }
      }
    },
    mouseWheel3(e) {
      this.part3srollY += e.wheelDeltaY * 0.25;
      if (this.part3srollY > 0) {
        this.part3srollY = 0;
        this.part3PicTop = this.part3srollY;
      } else if (this.part3srollY > ~this.part3BoxHeight) {
        this.part3PicTop = this.part3srollY;
      } else {
        this.part3srollY = ~this.part3BoxHeight;
        this.part3PicTop = ~this.part3BoxHeight;
      }

      this.part3SrollTop1 = this.part3srollY * 1.1;
      this.part3SrollTop1 = ~this.part3SrollTop1;
      this.part3SrollTop2 = this.part3srollY * 1.1;
      // 下面是页面跳转逻辑
      if (e.deltaY < 0) {
        this.part3Show = false;
      }
    },
    imgLoad2() {
      this.imgWidth2 = this.$refs.banner2.offsetWidth;
    },
    isDetail(is) {
      if (is) {
        //在这里要禁用鼠标不能滚动--背景板不能滚动
      } else {
        // 恢复鼠标滚轮事件
      }
    },
  },
};
</script>
<style scoped>
.mapLogo{
  position: absolute;
  z-index: 10000;
}
.part {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  transition: 0.7s linear;
  background: blue;
}
.part1 {
  position: relative;
  background-color: rgb(242, 219, 222);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 0.7s linear;
}
.part2 {
  transition: 0.5s linear;
  position: absolute;
  top: 0;
}
.part3 {
  position: absolute;
  top: 0;
  background-color: rgb(229, 207, 188);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 1s linear;
}
.part1-enter-active,
.part2-enter-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part1-leave-active,
.part2-leave-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-leave-active {
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-enter-active {
  transition: cubic-bezier(0.01, 0.9, 0.1, 0.1);
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.banner {
  height: 100vh;
  padding: 0 10%;
  position: absolute;
  top: 0;
  transition: 1s;
}
.banner img {
  height: 100vh;
}
.word {
  transition: 1s;
  position: absolute;
  left: 0;
  font-size: 110px;
  width: 100vw;
  padding-top: 50px;
  padding-bottom: 35vh;
}
.word p {
  font-family: QingKai;
  text-align: center;
  margin-top: 30px;
  border: 0;
}
.pFirst {
  font-size: 150px;
}
.build1Shu {
  position: absolute;
  transition: 2s;
}
.logo2 {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  z-index: 5;
}

.part2Index {
  background-color: #dae6f1;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.Box {
  width: 100%;
  height: 110vh;
  transition: 1s;
}
.Box1 {
  width: 100%;
  height: 100vh;
  position: relative;
}

.imgBox1 {
  width: 50vw;
  height: 110vh;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 0;
  transition: 1s;
  display: flex;
  align-items: center;
}
.img1 {
  position: absolute;
  width: 100%;
}
.content1 {
  font-family: XiuKai;
  transition: 1s;
}
.content1 {
  position: absolute;
  bottom: 20vh;
  left: 15vw;
  text-align: left;
  z-index: 10;
}
.content1P1 {
  font-size: 6.2rem;
}
.content1P2 {
  font-family: FangSong;
  font-size: 3.5rem;
}
/* 下面是part3的class */
.part3Index {
  background-color: #f2dbde;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.part3Box {
  width: 100%;
  height: 110vh;
  transition: 1s;
}
.part3Box1 {
  width: 100%;
  height: 100vh;
  position: relative;
}
.part3ImgBox1 {
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 10;
  top: 60%;
  transition: 1s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.part3Img1 {
  position: absolute;
  width: 90%;
}

.wordBox {
  width: 100%;
  height: 300vh;
  transition: 1s;
}

.wordBox1 {
  width: 100%;
  height: 100vh;
  position: relative;
}

.part3Content1,
.part3Content2 {
  font-family: XiuKai;
  transition: 1s;
  position: absolute;
  text-align: left;
}
.part3Content1 {
  height: 50%;
  z-index: 0;
  right: 0;
  display: flex;
  align-items: flex-end;
}
.part3Content2 {
  height: 50%;
  z-index: 20;
  bottom: 0%;
  display: flex;
  align-items: flex-start;
}
.part3Content1P1 {
  font-size: 7.8rem;
  white-space: nowrap;
  transition: 1s;
  color: #7c3f4e;
  text-align: left;
}
.part3Content1P2 {
  font-size: 7.8rem;
  white-space: nowrap;
  transition: 1s;
  color: #7c3f4e;
}
</style>
