<template>
  <div class="popupBox">
    <!-- <div></div> -->
    <img :src="logoImgSrc" class="logo2" @click="toDetail" />
    <div class="mask" v-show="isMask">
      <div class="imgBox">
        <img :src="mapImgSrc" class="imgStyle" />
        <div class="close" @click="close"></div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  props: {
    buildIndex: String,
  },
  mounted() {
    console.log(this.buildIndex);
    this.logoImgSrc = require(`../assets/mapLogo/logo${this.buildIndex}.png`);
    this.mapImgSrc = require(`../assets/map/map${this.buildIndex}.png`);
  },
  data() {
    return {
      isMask: false,
      logoImgSrc: "",
      mapImgSrc: "",
    };
  },
  methods: {
    close() {
      this.isMask = false;
      this.$emit("isDetail", this.isMask);
    },
    toDetail() {
      this.isMask = true;
      this.$emit("isDetail", this.isMask);
    },
  },
};
</script>

<style scoped>
.popupBox {
  display: flex;
  align-items: center;
  position: relative;
}
.mask {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 9999;
  top: 0;
  left: 0;
  /* right: -2rem;
  bottom: -2rem; */
}
.logo2 {
  width: 14vw;
  cursor: pointer;
  position: fixed;
  right: 2rem;
  bottom: 2rem;
}

.imgBox {
  width: 70%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.imgStyle {
  left: 50%;
  top: calc(50% + 2rem);
  width: 100%;
}
.close {
  width: 2.5vw;
  height: 2.5vw;
  background-image: url("../assets/buildInfo/build25/a业记素材-地图叉叉.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 10000;
}
</style>
